@import "@/styles/global.scss";


























html,
body,
#app {
  height: 100%;
  background-color: aliceblue;
}

.page-enter-active,
.page-leave-active {
  transition: all 1s;
}
.page-enter {
  opacity: 0;
  transform: translateX(-100px);
}
.page-leave-to {
  opacity: 0;
  transform: translateX(100px);
}
.page-move {
  transition: transform 1s;
}
