@import "@/styles/global.scss";








































$prefix: page-container;

##{$prefix} {
  width: 90%;
  height: 90%;
  border: 5px solid #4c8f38;
  margin: 0 auto;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: scroll;
  border-radius: 30px;
  ##{$prefix}-button-group {
    width: 100%;
    margin-top: 30px;
    & > button {
      margin-left: 30px;
    }
  }
  .red {
    color: red;
  }
  .pink {
    color: pink;
  }
}
