@import "@/styles/global.scss";



































































































.todo-item-container {
  display: grid;
  border: 2px solid #f0f0f0;
  grid-template-columns: 10% 80% 10%;
  justify-items: center;
  align-items: center;
  padding: 10px 0;
  margin-top: 30px;
  border-radius: 20px;
  font-size: 15px;
  & > div {
    width: 100%;
  }
  & > div:nth-child(2) {
    justify-self: start;
    align-items: start;
    .add-time {
      text-align: right;
    }
  }
}
