@import "@/styles/global.scss";

































#todoList {
  width: 80%;
  margin: 0 auto;
  .list-enter-active,
  .list-leave-active {
    transition: all 0.5s;
  }
  .list-enter {
    opacity: 0;
    transform: translateX(-100px);
  }
  .list-leave-to {
    opacity: 0;
    transform: translateX(100px);
  }
  .list-move {
    transition: transform 1s;
  }
  /* display: flex; */
  /*TODO: 子元素并未设置margin的时候，该属性也失效了 justify-content: center; */
  /* flex-direction: column; */
}
